.second-div{
    /* margin-top: 2%; */
    width: 80%;
    margin: 0% auto 3.5% auto;
}



.second-div h5 {
    text-align: center;
    margin-bottom: 2px;
    font-size: 15px;
    line-height: 20px;
    margin-top: 10px;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: rgb(0, 0, 0);
}


.second-div h4 {
    text-align: center;
    margin-bottom: 10px;
    font-size: 15px;
    line-height: 20px;
    margin-top: 50px;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: rgb(97, 97, 97);
}

.second-div h3 {
    text-align: center;
    margin-bottom: 10px;
    font-size: 12px;
    line-height: 20px;
    margin-top: 50px;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: rgb(97, 97, 97);
}

.para {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 15px;
    font-size: 3rem;
}

.divider{
    background-color: #eee;
    height: 1px;
    margin: .2rem 0rem 1.7rem 0rem;
}
