.inner-div{
    text-align: center;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
    align-items: center;
}

.space{
    margin-top: 1px;
}

.image{
    transition: 0.6s;
    height: auto;
    width: 100%;
}

.container{
    margin-top: 10px;
}

.overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #000000;
}

.text{
    color: rgb(255, 255, 255) !important;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
}

.container .overlay {
    opacity: 0.7;
    color: rgb(255, 255, 255);
  }

.size{
    padding: .8rem 1.2rem .8rem 1.2rem;
    margin-top: 15px;
    margin-right: 5px;
	border: none;
    cursor: pointer;
	background-color: black;
	color: white;
	font-size: 7px;
	letter-spacing: 3px;
	font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
