@media screen and (max-width: 768px) and (orientation : portrait){
    .second-div{
        margin-bottom: 14%;
    }
} 



@media screen and (max-width: 800px)  and (orientation : landscape){
    .second-div{
        margin-bottom: 10%;
    }
} 
