.header{
    width: 94vw;
    height: 85vh;
    margin: auto;
    background-color: rgb(0, 0, 0);
    color:rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    opacity: 0.9;
    animation: start-animation 2.5s ease;
}

@keyframes start-animation {
    from {
        opacity: 0;
    }

    to {
        opacity: 0.9;
    }
}


@keyframes h1-animation {
    0% {
        margin-right: 50%;
        
    }
    50% {
        margin-right: -5%;
        
    }

    100%{
        margin-right: 0%;
        
    }
}


@keyframes para-animation {
    0% {
        margin-left: 50%;
        
    }
    50% {
        margin-left: -5%;
        
    }

    100%{
        margin-left: 0%;
        
    }
}

@keyframes button-animation {
    0% {
        opacity: 0.5;
        
    }


    100%{
        opacity: 1;
        
    }
}


.second{
    margin-top: 100px
}
.header h1 {
    font-size: 5rem;
    line-height: 120px;
    animation: h1-animation 1.5s ease;
} 
 
.header p {
    line-height: 30px;
    animation: para-animation 1.5s ease;
} 

.button_learn_more{
    padding: .8em 1.2em .8em 1.2em;
    margin-top: 1.5rem;  
    background-color: #fff;
    color: black;
    border: none;
    border-radius: 1px;
    text-decoration: none;
    letter-spacing: 1.75px;
    animation: button-animation 1.5s ease;
}

.button_learn_more:hover{
    box-shadow: 1px 1px 15px 1px #888888;
    text-decoration: none;
    color: black;
}

