@media only screen and (min-device-width : 200px) and (max-device-width : 320px) {
    .header h1 {
        font-size: 1.2rem !important;
    }

    .header p {
        font-size: 0.5rem !important;
    }

    .header button {
        font-size: 0.55rem;
    }
}


@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
