        .header h1 {
            font-size: 4rem;
        }
    
        .header p {
            font-size: 1.5rem;
        }
    
        .header button {
            font-size: 0.95rem;
        }
    }
    
    /* iPads (portrait) ----------- */
    @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        .header h1 {
            font-size: 3rem;
        }
    
        .header p {
            font-size: 1.5rem;
        }
    
        .header button {
