  .about-section {
    background: url('../../static/images/justlines_kits_background.jpg') no-repeat left;
    background-size: 60%;
    overflow: hidden;
    padding: 50px 0;
  }
  
  .inner-container{
    width: 75%;
    direction: rtl;
    text-align: center;
    float: right;
    background-color: #fdfdfd;
    padding: 50px;
    /* box-shadow: 10px 10px 5px #aaaaaa; */
  }

  .inner-container h1 {
    margin-bottom: 10px;
    font-weight: 100;
    font-size: 13px;
    /* font-weight: 500; */
  }
  
  .text {
    font-size: 12px;
    color: black;
    line-height: 15px;
    text-align: justify;
    margin-bottom: 20px;
  
  }

  
.skills{
  display: flex;
  justify-content: space-between;
  font-weight: 100;
  font-size: 10px;
  }
}