.item{
    width: 60vw;
    background-color: #fff;
    margin: 0.75% auto auto auto;
    padding: 15px 15px 10px 15px;
    font-weight: bold;
    color: rgb(0, 0, 0);
    cursor: pointer;
    /* border-radius: 5px; */
    /* animation: MoveUpDown  */
}




