.main_general{
    background-color: #000000;
    font-family: inherit;
    height: 100vh;
    opacity: 0.9;
    padding-top: 3.5%;
    overflow-y: hidden;
    text-align: center;
}

.item {
    display: flex;
    justify-content: space-between;
    width: 60vw;
    background-color: #fff;
    margin: 0.75% auto auto auto;
    padding: 15px 15px 10px 15px;
    font-weight: bold;
    color: rgb(0, 0, 0);
    cursor: pointer;
}